import React, { useState } from 'react';
import { Typography, Input, Form, Button, Select, message, Modal } from 'antd';
import styles from '../css/Button.css';
import '../css/InputPage.css';

function InputMenuPage({  }) {

  const { Option } = Select;

  //        state
  const [weeklyMenu, setWeeklyMenu] = useState({
    monday: [],
    tuesday: [],
    wednesday: [],
    thursday: [],
    friday: [],
    saturday: [],
    sunday: []
  });
  const [selectedDay, setSelectedDay] = useState('monday'); //  
  const [inputValue, setInputValue] = useState('');
  const [modalVisible, setModalVisible] = useState(false);

  const onDescriptionChange = (event) => {
    const { value } = event.target;
    setInputValue(value);
  };

  const onDayChange = (value) => {
    setSelectedDay(value);
  };

  const onSubmit = event => {
    event.preventDefault();
    const newMenu = inputValue.split(/[, ]+/).filter(item => item.trim() !== ''); //      
    setWeeklyMenu(prevMenu => ({
      ...prevMenu,
      [selectedDay]: [...prevMenu[selectedDay], ...newMenu]
    }));
    setInputValue('');
    const formattedMenu = newMenu.join(', '); //     
    message.success(`${selectedDay.charAt(0).toUpperCase() + selectedDay.slice(1)}   : ${formattedMenu}`);
  };

  const deleteMenu = (day) => {
    setWeeklyMenu(prevMenu => ({
      ...prevMenu,
      [day]: []
    }));
    message.success(`${day.charAt(0).toUpperCase() + day.slice(1)}  .`);
  };

  const showModal = () => {
    setModalVisible(true);
  };

  const handleCancel = () => {
    setModalVisible(false);
  };

  return (
    <div className='inputPage' style={{ backgroundColor: 'beige', display: 'flex', alignItems: 'center', justifyContent: 'center', height: '100vh' }}>
      <div style={{ maxWidth: '700px', background: 'white', padding: '40px', borderRadius: '20px' }}>
        <div className='input-logo2'> </div>
        <Form onSubmit={onSubmit}>
          {/*   */}
          <Form.Item label="">
            <Select defaultValue="monday" onChange={onDayChange}>
              <Option value="monday"></Option>
              <Option value="tuesday"></Option>
              <Option value="wednesday"></Option>
              <Option value="thursday"></Option>
              <Option value="friday"></Option>
              <Option value="saturday"></Option>
              <Option value="sunday"></Option>
            </Select>
          </Form.Item>
          {/*       */}
          <Form.Item label="">
            <Input
              onChange={onDescriptionChange}
              value={inputValue}
            />
          </Form.Item>
          <div style={{ textAlign: 'center' }}>
            <Button className={`${styles.btn} }`} onClick={onSubmit}>
              
            </Button>
            <Button onClick={showModal} style={{ marginLeft: '10px' }}>  </Button>
          </div>
        </Form>
      </div>
      <Modal
        title=" "
        visible={modalVisible}
        onCancel={handleCancel}
        footer={[
          <Button key="back" onClick={handleCancel}></Button>
        ]}
      >
        {Object.keys(weeklyMenu).map(day => (
          <div key={day}>
            <p>{day.charAt(0).toUpperCase() + day.slice(1)}: {weeklyMenu[day].join(', ')}</p>
            <Button onClick={() => deleteMenu(day)} type="default"> </Button>
          </div>
        ))}
      </Modal>
    </div>
  );
}

export default InputMenuPage;
